.Pagination {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 1rem 0;
}

.Pagination-button {
  background-color: #3f51b5;
  border: none;
  color: white;
  cursor: pointer;
  font-size: 1rem;
  margin: 0 0.5rem;
  padding: 0.5rem 1rem;
  border-radius: 4px;
}

.Pagination-button:disabled {
  background-color: #9fa8da;
  cursor: not-allowed;
}

.Pagination-input {
  width: 50px;
  text-align: center;
  padding: 0.5rem;
  margin-left: 0.5rem;
  border-radius: 4px;
  border: 1px solid #3f51b5;
}
